import Vue from 'vue'
import Router from 'vue-router'
import App from '../App'

Vue.use(Router)

// const login = r => require.ensure([], () => r(require('../page/login/login')), 'login')

const login = () => import(/* webpackChunkName: "headFactory" */ (`@/page/login/login`))
const testHome = () => import(/* webpackChunkName: "headFactory" */ (`@/page/test/testHome`))
const testClassManage = () => import(/* webpackChunkName: "headFactory" */ (`@/page/test/testClassManage`))
const testStudentManage = () => import((`@/page/test/testStudentManage`))
const classManagePage = () => import(/* webpackChunkName: "headFactory" */ (`@/page/view/classManagePage`))
const homePage = () => import(/* webpackChunkName: "headFactory" */ (`@/page/view/homePage`))
const studentManagePage = () => import((`@/page/view/studentManagePage`))
const registerTimeManagePage = () => import((`@/page/view/registerTimeManagePage`))
const studentRegisterManagePage = () => import((`@/page/view/studentRegisterManagePage`))
export default new Router({
  routes: [
    {
      path: '/',
      // name: 'HelloWorld',
      component: App, //  顶层路由，挂载到app.vue中的router-view,而App.vue是挂载到index.html
      children: [ //  二级路由,对应App.vue
        // 地址为空时跳转home页面
        {
          path: '/login',
          component: login
        },
        {
          path: '/testHome',
          component: testHome

        },
        {
          path: '/testClassManage',
          component: testClassManage
        },
        {
          path: '/testStudentManage',
          component: testStudentManage
        },
        {
          path: '/classManagePage',
          component: classManagePage

        },
        {
          path: '/studentManagePage',
          component: studentManagePage

        },
        {
          path: '/homePage',
          component: homePage
        },
        {
          path: '/registerTimeManagePage',
          component: registerTimeManagePage
        },
        {
          path: '/studentRegisterManagePage',
          component: studentRegisterManagePage
        }

      ]
    }
  ]
})



